___CSS_LOADER_EXPORT___.push([module.id, `.restaurant {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
  padding: 32px;
}

.restaurant__poster {
  width: 100%;
  max-width: 400px;
  height: auto;
  border-radius: 8px;
}

.restaurant__info h4 {
  margin: 8px 0;
}

.restaurant__description {
  margin-top: 16px;
}

.restaurant__menus {
  margin-top: 32px;
}

.restaurant__menu-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  margin-top: 16px;
}

.restaurant__reviews {
  margin-top: 32px;
}

.review {
  margin-top: 16px;
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.detail-review {
  display: grid;
}

.detail-review-item {
  color: var(--secondary-color);
}

.review__name {
  font-weight: 500;
}

.review__date {
  color: #666;
  font-size: 14px;
  margin: 4px 0;
}

.form-review input[type="text"], 
.form-review button {
  min-width: 44px;
  min-height: 44px;
  padding: 10px;
  box-sizing: border-box;
}

.form-review button {
  background-color: #db0000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.form-review button:hover {
  background-color: #850303;
}

.form-review .mb-3 {
  margin-bottom: 1rem;
}

.form-review {
  max-width: 400px;
  margin: auto;
}

.like {
  font-size: 18px;
  position: fixed;
  bottom: 16px;
  right: 16px;
  background-color: #db0000;
  color: white;
  border: 0;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}`, "",{"version":3,"sources":["webpack://./src/styles/pages/detail.css"],"names":[],"mappings":"AAAA;EACE,cAAc;EACd,WAAW;EACX,gBAAgB;EAChB,aAAa;EACb,0BAA0B;EAC1B,cAAc;EACd,aAAa;AACf;;AAEA;EACE,WAAW;EACX,gBAAgB;EAChB,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,SAAS;EACT,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,6BAA6B;AAC/B;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,eAAe;EACf,aAAa;AACf;;AAEA;;EAEE,eAAe;EACf,gBAAgB;EAChB,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,eAAe;EACf,eAAe;EACf,YAAY;EACZ,WAAW;EACX,yBAAyB;EACzB,YAAY;EACZ,SAAS;EACT,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB","sourcesContent":[".restaurant {\n  margin: 0 auto;\n  width: 100%;\n  max-width: 800px;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 18px 16px;\n  padding: 32px;\n}\n\n.restaurant__poster {\n  width: 100%;\n  max-width: 400px;\n  height: auto;\n  border-radius: 8px;\n}\n\n.restaurant__info h4 {\n  margin: 8px 0;\n}\n\n.restaurant__description {\n  margin-top: 16px;\n}\n\n.restaurant__menus {\n  margin-top: 32px;\n}\n\n.restaurant__menu-list {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  gap: 16px;\n  margin-top: 16px;\n}\n\n.restaurant__reviews {\n  margin-top: 32px;\n}\n\n.review {\n  margin-top: 16px;\n  padding: 16px;\n  border: 1px solid #ddd;\n  border-radius: 8px;\n}\n\n.detail-review {\n  display: grid;\n}\n\n.detail-review-item {\n  color: var(--secondary-color);\n}\n\n.review__name {\n  font-weight: 500;\n}\n\n.review__date {\n  color: #666;\n  font-size: 14px;\n  margin: 4px 0;\n}\n\n.form-review input[type=\"text\"], \n.form-review button {\n  min-width: 44px;\n  min-height: 44px;\n  padding: 10px;\n  box-sizing: bord